@-webkit-keyframes Logo-spin {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes Logo-spin {
  from {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.Logo {
  padding-right: 16px;
  height: 64px; }
  .Logo #gear {
    -webkit-transform-origin: 50.75% 53%;
        -ms-transform-origin: 50.75% 53%;
            transform-origin: 50.75% 53%; }

.Logo.spinning #gear {
  -webkit-animation: Logo-spin 10s infinite linear;
          animation: Logo-spin 10s infinite linear; }

.Logo.error #gear {
  fill: #fe4d1c; }



